* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  body {
    background: linear-gradient(to right, darkslateblue, lightblue);
    color: white;
    font-family: "Poppins", sans-serif;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .todo-app {
    width: 50rem;
    padding: 2rem;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  }
  
  header {
    font-size: 2rem;
    text-align: center;
    margin-bottom: 1.5rem;
  }
  
  form {
    display: flex;
    justify-content: center;
    margin-bottom: 1.5rem;
  }
  
  form input {
    height: 3.5rem;
    width: 20rem;
    border-radius: 5px;
    border: none;
    margin-right: 0.5rem;
  }
  
  form button {
    height: 3.5rem;
    width: 10rem;
    background:  rgb(12, 45, 89);
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 1.5rem;
    margin-right: 0.5rem;
    border-radius: 2rem;
    
  }
  
  form button:hover {
    background:  rgb(69, 112, 167);
  }
  
  .todo-list {
    list-style: none;
  }
  
  .todo-item {
    display: flex;
    align-items: center; /* Align checkbox and text vertically */
    justify-content: space-between;
    padding: 1rem;
    background: rgba(255, 255, 255, 0.2);
    margin: 0.5rem 0;
    border-radius: 5px;
    font-size: 2rem;
  }
  
  .todo-item.completed {
    text-decoration: line-through;
    opacity: 0.5;
  }
  
  .todo-item input[type="checkbox"] {
    margin-right: 1rem; 
    height: 1rem;
    width: 1rem;
  }
  
  .todo-item button {
    background: rgb(12, 45, 89);
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 1rem;
    padding: 1rem;
  }
  
  .todo-item button:hover {
    background: darkred;
  }